import React from 'react';
import { inject, observer } from 'mobx-react';
import { Modal } from 'antd';


import './adjust_product_discount.less';
@inject('settlementStore') @observer
class AdjustProuductDiscount extends React.Component {

  handleOk=()=>{
   this.props.settlementStore.moreokClick();
  }
  handleCancel=()=>{
   
     this.props.settlementStore.closeAdjusetProduct();
  }
  render() {
    return (
        <Modal title="" 
            visible={true} 
            closable={true}
            maskClosable={false}
            width={900}
            wrapClassName="adjust-product-discount-modal"
            onOk={this.handleOk}
            onCancel={this.handleCancel}
        >
          <div className="discount-content">
            <ul className="discount-title">
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
            <ul className="discount-body">
              <li>
                <span>1</span>
                <span></span>
                <span>2</span>
                <span>100</span>
                <span>9</span>
                <span>180</span>
                <span>20.00</span>
                <span><input value="9" onChange={(e)=>{
                    e.target.value
                }} className="discount"/></span>
                <span><input value="20.00" className="discount-amount"/></span>
                <span className="recover-discount"></span>
              </li>
              <li>
                <span>1</span>
                <span></span>
                <span>2</span>
                <span>100</span>
                <span>9</span>
                <span>180</span>
                <span>20.00</span>
                <span><input value="9" className="discount"/></span>
                <span><input value="20.00" className="discount-amount"/></span>
                <span className="recover-discount"></span>
              </li>
            </ul>
          </div>
           <div className="bottom-detail">
              <span><i>26.00</i></span>
              <span><i>24.00</i></span>
              <span className="balance"><i>2.00</i></span>
            </div>
        </Modal>
    )
  }
}

export default AdjustProuductDiscount;


