import React from 'react';
import { Modal } from 'antd';
import { inject, observer } from 'mobx-react';

import MyScroll from 'components/my-scrollbar';

import './card_tuikuan_popup.less';


@inject('settlementStore') @observer
class TuikuanPopup extends React.Component {
  render() {
     
     
    return <Modal title={this.props.title+""} 
        visible={true} 
        maskClosable={false}
        onOk={()=>{
            if(this.props.onOk){
                this.props.onOk();
            }
        }} 
        okText='' cancelText=''
        width={840} wrapClassName="card-tuikuan-popup-modal"
        onCancel={()=>{
            if(this.props.onCancel){
                this.props.onCancel();
            }
        }}>

        <div className="tuikuan-block">
            <div className="tuikaun-money">
                <p></p>
                <div className="money-content">{this.props.title}<i>{this.props.textValue}</i></div>
            </div>
            <div className="tuikuan-cause">
                <div className="cause-header"></div>
                <MyScroll width={800} height={148}>
                    <li className="btn-240-44 btn-active"></li>
                    <li className="btn-240-44"></li>
                    <li className="btn-240-44"></li>
                    <li className="btn-240-44"></li>
                    <li className="btn-240-44"></li>
                </MyScroll>
            </div>
            <textarea type="text" placeholder="" className="tuikaun-remarks">
            </textarea>
        </div>
    </Modal>
  }
}



export default TuikuanPopup;


